import { FC, useEffect, useState } from 'react';
import { GeneralContainer } from '..';
import { YearCalendarProps } from './types-year-calendar';
import {
  day,
  emp,
  leapMonthDay,
  months,
  monthDay,
  startDate,
  weeks,
} from '@src/data/calendar-data';
import {
  Box,
  Card,
  DayCard,
  DisplayBox,
  Wrapper,
} from './styles-year-calendar';

const YearCalendar: FC<YearCalendarProps> = ({ year = '2005' }) => {
  const [countEmp, setCountEmp] = useState<number[]>([startDate[year]]);
  const [active, setActive] = useState<string>('');
  const [asMonthDay, setAsMonthDay] = useState<number[]>(
    parseInt(year) % 4 === 0 ? leapMonthDay : monthDay
  );
  useEffect(() => {
    let temp = countEmp;
    Object.keys(months).map((item, idx) => {
      if (idx <= 11) {
        temp.push((asMonthDay[idx] + temp[idx]) % 7);
      }
    });
    setCountEmp([...temp]);
  }, []);
  useEffect(() => {
    console.log('active: ', active);
  }, [active]);
  return (
    <GeneralContainer>
      <Wrapper>
        <h1>{year}-yil</h1>
        <Card>
          {Object.keys(months).map((month, index) => (
            <Box>
              <label>{month}</label>
              <DisplayBox>
                {weeks.map((week, idx) => (
                  <span key={idx}>{week}</span>
                ))}
              </DisplayBox>
              <DisplayBox>
                {emp.map(
                  (item) => item <= countEmp[index] && <div key={item}></div>
                )}
                {day.map((item, idx1) => (
                  item <= asMonthDay[index] && <DayCard
                    active={year + '.' + months[month] + '.' + item === active}
                    key={idx1}
                    onClick={() => {
                      setActive(year + '.' + months[month] + '.' + item);
                    }}
                  >
                    <p>{item}</p>
                  </DayCard>
                ))}
              </DisplayBox>
            </Box>
          ))}
        </Card>
      </Wrapper>
    </GeneralContainer>
  );
};

export default YearCalendar;
